import React from 'react'
import { Modal, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { togglePlanSection, togglePriceSection } from '../../../../actions/show_modal_Action';
import { useDispatch } from 'react-redux'; 
import { CLOUDPATH_TAGGSHOP } from '../../../../actions/api';

export const UpdateTimeModal = ({ show, modalHandleClose }) => {

    const dispatch = useDispatch()

    return (
        <Modal show={show}
            onHide={modalHandleClose}
            backdrop="static"
            size="md"
            keyboard={false} centered>
            <div className="modal-body upgrade_modal">
                <button type="button" className="btn-close" onClick={modalHandleClose}></button>
                <div className="py-6">
                    <div className="pb-5 text-center">
                        <img className="img-fluid mb-7" src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/svg/upgrade-svg.svg`} height={140} width={140} alt="Time Update" />
                        <h3 className="text-center mb-3">Automatic content update time</h3>
                        <p className="font-weight-bold text-dark-75 text-center">
                            Update time means how fast the new content will be updated automatically on your Social feeds/walls.
                        </p>
                    </div>
                    <div className="pb-5 table-responsive">
                        <table className="table table-sm table-center">
                            <tbody>
                                <tr>
                                    <th>Plans</th>
                                    <td>Growth</td>
                                    <td>Professional</td>
                                    <td>Premium</td>
                                </tr>
                                <tr>
                                    <th>Update Time</th>
                                    <td> 2 Hour</td>
                                    <td> 1 Hour</td>
                                    <td>30 Minutes</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div className="text-center">
                        <button type="button" className="btn btn-primary upgPlan" data-bs-dismiss="modal" onClick={() => { dispatch(togglePriceSection(true, 2)); modalHandleClose() }}>Upgrade Plan</button>
                    </div>
                </div>
                <OverlayTrigger placement="right" overlay={<Tooltip>Know More</Tooltip>}>
                    <a href="https://taggbox.com/support/what-is-update-time/" target="_blank" className="modal_tooltip">
                        <span className="svg-icon svg-icon-muted svg-icon-lg">
                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                <g id="Group_4423" data-name="Group 4423" transform="translate(10951 -4685)">
                                    <g id="Group_4414" data-name="Group 4414" transform="translate(-10951 4685)" opacity="0">
                                        <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" fill="none"></rect>
                                    </g>
                                    <g id="Group_4418" data-name="Group 4418" transform="translate(-10950.447 4685.553)">
                                        <path id="Path_5209" data-name="Path 5209" d="M11.447,2a9.447,9.447,0,1,0,9.447,9.447A9.447,9.447,0,0,0,11.447,2Zm0,17.005a7.558,7.558,0,1,1,7.558-7.558A7.558,7.558,0,0,1,11.447,19.005Z" fill="#545454"></path>
                                        <g id="Group_4421" data-name="Group 4421" transform="translate(0.325 0.572)">
                                            <path id="Path_5210" data-name="Path 5210" d="M11.482,6A2.982,2.982,0,0,0,8.5,8.982a.852.852,0,1,0,1.7,0,1.278,1.278,0,1,1,1.278,1.278.852.852,0,0,0-.852.852v1.7a.852.852,0,0,0,1.7,0v-.988A2.974,2.974,0,0,0,11.482,6Z" transform="translate(-0.359 -0.221)" fill="#545454"></path>
                                            <circle id="Ellipse_161" data-name="Ellipse 161" cx="1" cy="1" r="1" transform="translate(10.122 14.375)" fill="#545454"></circle>
                                        </g>
                                    </g>
                                </g>
                            </svg>
                        </span>
                    </a>
                </OverlayTrigger>
            </div>
        </Modal>
    )
}
