import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { TAGGBOX_USER_HOME_DATA, TAGGSHOP_SHOPPABLE_GALLERY_DATA } from '../../../../actions/sceneActions'
import { useNavigate } from 'react-router'
import { SHOPPABLE_GALLERY_DATA } from '../../../../reducer/keys'

export const HeaderCollabrator = ({ collabratorList }) => {
    const [selectedCollabrator, setSelectedCollabrator] = useState(null)
    const dispatch = useDispatch()
    const navigate = useNavigate()


    const onCollabChange = (collab) =>  {
        localStorage.setItem("isCollaborator", collab.role ? collab.role : 0)
        localStorage.setItem("ownerId", collab.userId)
        localStorage.setItem('collab_token', collab.collabratorAccessToken)
        dispatch({type: SHOPPABLE_GALLERY_DATA, payload: null})
        setSelectedCollabrator(collab)
        dispatch(TAGGBOX_USER_HOME_DATA())
        dispatch(TAGGSHOP_SHOPPABLE_GALLERY_DATA());
        navigate(`/content/moderation/${localStorage.getItem('wallId')}`)
        // window.location.reload()
    }
    
    return (
        <div className="dropdown">

            <div className="topbar-item" data-bs-toggle="dropdown" data-offset="10px,0px" aria-expanded="false">
                <a  className="btn btn-icon w-auto d-inline-flex align-items-center btn btn-lg btn-outline-secondary px-3 pr-2 mr-3">
                    <span className="font-weight-bolder font-size-base mr-2">{selectedCollabrator ? selectedCollabrator?.name : collabratorList[0].name}</span>
                    <span className="svg-icon">
                        <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                            <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                <polygon points="0 0 24 0 24 24 0 24"></polygon>
                                <path id="arrow_light" d="M6.70710678,15.7071068 C6.31658249,16.0976311 5.68341751,16.0976311 5.29289322,15.7071068 C4.90236893,15.3165825 4.90236893,14.6834175 5.29289322,14.2928932 L11.2928932,8.29289322 C11.6714722,7.91431428 12.2810586,7.90106866 12.6757246,8.26284586 L18.6757246,13.7628459 C19.0828436,14.1360383 19.1103465,14.7686056 18.7371541,15.1757246 C18.3639617,15.5828436 17.7313944,15.6103465 17.3242754,15.2371541 L12.0300757,10.3841378 L6.70710678,15.7071068 Z" fill="#fff" fillRule="nonzero" transform="translate(12.000003, 11.999999) rotate(-180.000000) translate(-12.000003, -11.999999) "></path>
                            </g>
                        </svg>
                    </span>
                </a>
            </div>


            <div className="dropdown-menu p-0 m-0 dropdown-menu-anim-up dropdown-menu-sm dropdown-menu-right">

                <ul className="navi navi-hover py-2">

                    {collabratorList.map(collab => (
                        <li className="navi-item" onClick={() => onCollabChange(collab)}>
                            <a 
                            // href="accounts/changeowner/76636"
                             className="navi-link px-5 py-2 m_acoount">
                                <span className="symbol symbol-30 symbol-circle symbol-light-warning mr-3">
                                    <div className="symbol-label" style={collab.picture.includes('https://') ? { backgroundImage: `url('${collab.picture}')` } : {}}>{!collab.picture.includes('https://') ? collab.picture : ""}</div>
                                </span>
                                <span className="navi-text">{collab.name}</span>
                            </a>
                        </li>
                    ))}


                    {/* <li className="navi-item">
                        <a href="accounts/changeowner/76563" className="navi-link px-5 py-2">
                            <span className="symbol symbol-30 symbol-circle symbol-light-warning mr-3">
                                <div className="symbol-label" style={{ backgroundImage: "url('https://www.ekcreditunion.co.uk/wp-content/uploads/2018/02/Blank-Silhouette.jpg')" }}></div>
                            </span>
                            <span className="navi-text">Arnodya</span>
                        </a>
                    </li> */}

                </ul>

            </div>

        </div>
    )

   
}
