import { useEffect } from "react";
import { useState } from "react";

const ProgressBar = ({ progress, strokeWidth, radius }) => {
    const normalizedRadius = radius - strokeWidth * 2;
    const circumference = normalizedRadius * 2 * Math.PI;
    const strokeDashoffset = circumference - ((progress) / 3) * circumference;

    const [offset, setOffset] = useState(0);

    useEffect(() => {
        setOffset(strokeDashoffset);
    }, [strokeDashoffset]);

    return (
        <svg height={radius * 2} width={radius * 2}>
            <circle
                stroke="#DBE1E5" // Outline color
                fill="transparent"
                strokeWidth={strokeWidth}
                r={normalizedRadius}
                cx={radius}
                cy={radius}
            />
            <circle
                stroke="#6DC15A" // Progress color
                fill="transparent"
                strokeWidth={strokeWidth}
                strokeDasharray={circumference + ' ' + circumference}
                style={{ strokeDashoffset }}
                r={normalizedRadius}
                cx={radius}
                cy={radius}
            />
            <text
                x="50%"
                y="50%"
                textAnchor="middle"
                dominantBaseline="middle"
                fontSize="12"
                fill="#606367" // Text color
            >
                {`${progress}`}
            </text>
        </svg>
    );
};

export default ProgressBar